import { Observable } from "rxjs";
import { Injectable } from "@angular/core";

import { HttpClient, HttpParams } from "@angular/common/http";
import { environment } from "@env/environment";
import { AvailabilityWithMatchesDto, MatchDto } from "../model/availability";
import { PlayingFieldSetupDto, SearchDto } from "../model/playing-field";

const API_URL = environment.apiUrl;

@Injectable({
  providedIn: "root"
})
export class PlayingFieldService {
  constructor(private http: HttpClient) {}

  deletePFAvailability(id) {
    return this.http.delete(
      `${API_URL}/worker/playingField/playingFieldAvailability/${id}`
    );
  }
  getPFSetup(id) {
    return this.http.get<PlayingFieldSetupDto>(
      `${API_URL}/playingField/${id}/setup`
    );
  }

  getPFAvailabilities(id, showAll): Observable<AvailabilityWithMatchesDto[]> {
    return this.http.get<AvailabilityWithMatchesDto[]>(
      `${API_URL}/playingField/availabilities/${id}`,
      { params: { showAll: showAll } }
    );
  }

  getMatches(id, searchDto: SearchDto, httpParams?: HttpParams | any): Observable<any[]> {
    return this.http.post<any[]>(
      `${API_URL}/playingField/${id}/matches/search`,
      searchDto, {params: httpParams}
    );
  }
}
